<template>
  <div class="browse">
    <h1>This is a browse page</h1>
    <div v-if="isAdmin">
      <add-show @submitted-show="getShows"></add-show>
    </div>
    <browse-filter @browser-filter-change="setQuery" />
    <browse-show :isAdmin="isAdmin" :showsArr="shows" :loggedInUser="user" />
    <!-- Show cards -->
  </div>
</template>
<style lang="scss">
.fa-sort-alpha-up,
.fa-sort-alpha-down,
.fa-tags,
.fa-check {
  color: red;
}

.fa-play {
  color: greenyellow;
}

.fa-calendar-alt {
  color: rgb(22, 0, 218);
}

#filter-show-cards,
#filter-selection-cards {
  color: black;
}
</style>
<script>
import { mapActions } from 'vuex';
import AddShow from '../components/AddShow.vue';
import BrowseFilter from '../components/BrowseFilter.vue';
import BrowseShow from '../components/BrowseShow.vue';
import DataService from '../../service/dataService';
export default {
  components: { BrowseFilter, BrowseShow, AddShow },
  data() {
    return {
      role: 'User',
      isAdmin: false,
      shows: [],
      query: {
        country: '',
        genre: '',
        airingStatus: '',
        yearStart: '',
        yearEnd: '',
        searchText: '',
      },
      user: {},
    };
  },
  name: 'Browse',
  computed: {
    getUser() {
      return this.$store.getters.getUser;
    },
    getRole() {
      return this.$store.getters.getRole;
    },
  },
  async mounted() {
    const localToken = localStorage.getItem('userToken');
    if (!localToken) {
      this.$router.push('/');
    } else {
      console.log('GET USER: ', this.getUser);
      if (!this.getUser) {
        console.log('INSIDE');
        this.login(localToken);
        this.user = this.getUser;
        console.log('BROWSER USER1: ', this.user);
      } else {
        this.user = this.getUser;
        console.log('BROWSER USER2: ', this.user);
      }
      console.log('Browse mount');
    }
    this.$store.subscribe((setRole, role) => {
      console.log('TYPE: ', setRole.type);
      console.log('PAYLOAD: ', setRole.payload);
      console.log('ROLE Browser: ', role);
      this.role = role.payload;
    });
    console.log('BROWSE GET ROLE: ', this.getRole);
    this.role = this.getRole;
    if (this.role === 'Admin') {
      this.isAdmin = true;
    }
    await this.getShows();
  },
  watch: {
    query: {
      deep: true,
      async handler() {
        await this.getShows();
      },
    },
  },
  methods: {
    ...mapActions(['login']),
    async getShows() {
      console.log('REACHED GET SHOWS ', this.query);
      this.shows = await DataService.getShowBrowseFilter(this.query);
      console.log('SHOWS!!!!!! ', this.shows);
    },
    setQuery(newQuery) {
      this.query = newQuery;
    },
  },
};
</script>
